#dropdownIcon {
	border: 1px solid black;
	padding: 5px 2px 2px;
	height: 16px;
}

.dropdownItem {
	padding: 5px 2px 2px;
	z-index: 100;
	height: 16px;
	opacity: 1;
	transition: all 3s ease 2s;
}

/* #itemReady {
	opacity: 1;
	height: 16px;
	z-index: 99;
} */
.dropdownContainer {
	position: absolute;
	right: 0;
	width: 105px;
	top: 50%;
	transform: translateY(calc(-50% - 26.5px));
	height: 25px;
	opacity: 1;
	cursor: pointer;
	z-index: 1;
}

.itemsOuter {
	height: 150px;
	border: 1px solid black;
}

.itemsInner {
	position: relative;
	height: 0px;
	opacity: 0;
	border: 2px solid green;
	transition: all 2s ease;
	z-index: 99;
}

.itemsInnerReady {
	opacity: 1;
	height: 150px;
}
