html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
body {
	line-height: 1;
}
ol,
ul {
	list-style: none;
}
blockquote,
q {
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
	content: "";
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
	max-width: 100vw;
	overflow-x: hidden;
}

.pageContainer {
	text-align: center;
	display: flex;
	flex-flow: column;
	min-height: 100vh;
	height: 100%;
	text-align: center;
	background: url("../../images/white-waves.png");
	opacity: 0;
	transition: opacity 1s ease-in;
}

main {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 0 auto;
	width: 100%;
	flex: 1 1 auto;
	align-content: baseline;
	padding-bottom: 20px;
	padding-top: 10px;
	/* background: url("../../images/white-waves.png"); */
	/* padding-top: 40px; */
	/* background-image: url("../../images/light-grey-terrazzo.png"); */
	/* background: url("../../images/white-waves.png"); */
}

footer {
	flex: 0 1 50px;
	background: rgb(187, 222, 243);
	/* justify-content: center;
	align-items: center; */
	line-height: 53px;
	border-top: 2px solid blue;
	font-size: 17px;
	font-family: "Kumbh Sans", sans-serif;
}

#changeOpacity {
	opacity: 1;
}
