#postForm {
	max-width: 500px;
	width: 100%;
	margin: 0 auto;
	padding: 20px;
	background-color: rgb(187, 222, 243);
	border-radius: 10px;
	position: relative;
	border: 1px solid black;
}

#heading {
	flex: 1 1 100%;
	padding-bottom: 20px;
	font-size: 25px;
	font-family: "Kufam", cursive;
}

.formGroup {
	/* position: relative; */
	padding-bottom: 15px;
	flex: 1 1 100%;
	/* width: fit-content; */
	margin: 0 auto;
	display: flex;
	justify-content: center;
	align-items: baseline;
}

#closeModalBtn {
	position: absolute;
	right: -10px;
	top: -10px;
	cursor: pointer;
	background-color: red;
	color: white;
	border: none;
	outline: none;
	border-radius: 10px;
	padding-bottom: 3px;
}

/* #closeModalBtn:hover {
	opacity: 0.7;
} */

.btn {
	cursor: pointer;
	border: 1px solid black;
	outline: none;
	padding-left: 5px;
	padding-right: 5px;
	padding-top: 3px;
	padding-bottom: 3px;
	border-radius: 3px;
}

.btn:hover {
	opacity: 0.7;
}

#postInput {
	/* flex: 1 1 100%; */
	/* display: flex; */
	position: relative;
	width: 100%;
	max-width: 300px;
	padding-top: 3px;
	padding-bottom: 3px;
	margin-left: 5px;
	font-size: 16px;
	margin-top: 5px;
	/* float: right; */
}

textarea {
	resize: none;
	height: 200px;
	font: 16px Times;
}

#fieldContainer {
	display: flex;
	flex-wrap: wrap;
	/* align-items: baseline; */
	/* justify-content: center;
    align-content: center; */
}

.inputLabel {
	font-family: "Kumbh Sans", sans-serif;
	font-size: 17px;
}
